import React from 'react';
import './Projects.css';


function Projects(props) {
    return (
        <section className='project-list'>
            <div className='project'>
                <header className='project-header'>
                    <a href="https://hoodify.netlify.app/" target="_blank"><img className='project-screen' src="/Hoodify_Screenshot.png" alt="Hoodify" /></a>
                </header>
                <section className='project-details'>
                    <span className="project-title">Hoodify</span>
                    <p className='project-description'>A basic Next.JS E-commerce app with product pages and shopping cart functionality.</p>
                    <div className="project-links">
                        <a href="https://github.com/nickisnik/hoodify" target="_blank"><img src="/GitHub-Mark-Light-32px.png"></img></a>
                        <a href="https://hoodify.netlify.app/" target="_blank"><button>Demo</button></a>
                    </div>
                </section>
            </div>
            <div className='project'>
                <header className='project-header'>
                    <a href="https://memorycardz.netlify.app/" target="_blank"><img className='project-screen' src="/MemoryCard_screen.png" alt="Memory card" /></a>
                </header>
                <section className='project-details'>
                    <span className="project-title">Memory Card</span>
                    <p className='project-description'>A React memory card game with random thematic images fetched from a public API. Goal: don't click on the same card twice!</p>
                    <div className="project-links">
                        <a href="https://github.com/nickisnik/react-memory-card" target="_blank"><img src="/GitHub-Mark-Light-32px.png"></img></a>
                        <a href="https://memorycardz.netlify.app/" target="_blank"><button>Demo</button></a>
                    </div>
                </section>
            </div>
            <div className='project'>
                <header className='project-header'>
                    <a href="https://exoqueries.vercel.app/" target="_blank"><img className='project-screen' src="/Exo_screen.png" alt="" /></a>
                </header>
                <section className='project-details'>
                    <span className="project-title">Exoqueries</span>
                    <p className='project-description'>This Next.JS app displays a random exoplanet and its info, fetched from NASA's API. It also shows a realistic Earth size comparison.</p>
                    <div className="project-links">
                        <a href="https://github.com/nickisnik/exoqueries" target="_blank"><img src="/GitHub-Mark-Light-32px.png"></img></a>
                        <a href="https://exoqueries.vercel.app/" target="_blank"><button>Demo</button></a>
                    </div>
                </section>
            </div>
            <div className='project'>
                <header className='project-header'>
                    
                </header>
                <section className='project-details'>
                    <span className="project-title">Coming soon...</span>
                    <p className='project-description'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt exercitationem cumque quia ipsum!</p>
                    <div className="project-links">
                        <a href=""><img src="/GitHub-Mark-Light-32px.png"></img></a>
                        <a href=""><button>Demo</button></a>
                    </div>
                </section>
            </div>
            
        </section>
    );
}

export default Projects;